import React from 'react'
import {MuiThemeProvider, createMuiTheme} from 'material-ui/styles'
import {BrowserRouter, Route} from 'react-router-dom'

import Nav from './nav/Nav'
import Calc from './calc/Calc'
import BulkCalc from './calc/BulkCalc'
import Setup from './setup/Setup'
import Milestones from './milestones/Milestones'

const theme = createMuiTheme({
    palette: {
        type: 'dark'
    }
})

class App extends React.Component {
    render() {
        return (
            <BrowserRouter>
                <MuiThemeProvider theme={theme}>
                    <div>
                        <Nav/>

                        <Route exact path={process.env.PUBLIC_URL + '/'} component={Calc}/>
                        <Route path={process.env.PUBLIC_URL + '/calc'} component={Calc}/>
                        <Route path={process.env.PUBLIC_URL + '/bulk'} component={BulkCalc}/>
                        <Route path={process.env.PUBLIC_URL + '/setup'} component={Setup}/>
                        <Route path={process.env.PUBLIC_URL + '/milestones'} component={Milestones}/>
                    </div>
                </MuiThemeProvider>
            </BrowserRouter>
        )
    }
}

export default App



